.track {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
  margin-right: auto;
  margin-left: auto;
  /* background-color: red; */
}

.album-img {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  margin-left: auto;
}

.album-img img {
  width: 100%;
  margin: auto;
}

@media (min-width: 768px) {
  .album-img img {
    width: 100%;
    margin: auto;
  }
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 32px;
}

.track-name {
  text-align: center;
  margin-bottom: 1rem;
}

.album-name {
  text-align: center;
  margin-top: 1rem;
}